import React, { useState, useEffect, useContext } from "react";
import { Line } from "react-chartjs-2";
import { Chart, registerables } from "chart.js";
import AppContext from "../../context/context";
import { currencyFormatter } from "../../context/invest.utls";
import classNames from "classnames";

Chart.register(...registerables);

const GrowthChart = (props) => {
  const appCtx = useContext(AppContext);
  const [chartData, setChartData] = useState(null);
  // eslint-disable-next-line
  const [filter, setFilter] = useState("yearly");
  const [intervalInYearly, setIntervalInYearly] = useState(10);

  const activeCollection = appCtx.getActiveCollection();
  const monthlyRate = 0.006;
  const monthlyInvestment = activeCollection ? activeCollection.totalPrice : 0;
  const staticIntervalFilters = [4, 8, 10, 12, 15, 18, 20, 25];
  const intervals = {
    yearly: intervalInYearly, // 10 years
  };

  const getRandomFluctuation = () => {
    const maxFluctuation = 0.1;
    return (Math.random() * 2 - 1) * maxFluctuation;
  };

  const generateInvestmentData = (filterType) => {
    let legends = intervals[filterType];
    let cumulativeInvestment = 0;
    const investmentValues = [];

    for (let i = 0; i < legends; i++) {
      const rateWithFluctuation = monthlyRate + getRandomFluctuation();
      cumulativeInvestment =
        (cumulativeInvestment + monthlyInvestment) * (1 + rateWithFluctuation);
      investmentValues.push(cumulativeInvestment.toFixed(2));
    }

    return investmentValues;
  };

  const generateLabels = (filterType) => {
    const length = intervals[filterType];
    return Array.from({ length }, (_, i) => i + 1);
  };

  useEffect(() => {
    const investmentValues = generateInvestmentData(filter);
    const labels = generateLabels(filter);

    const data = {
      labels: labels,
      datasets: [
        {
          label: "Investment Value Over Time",
          data: investmentValues,
          borderColor: "#2B2B2B",
          borderWidth: 2,
          fill: false,
        },
      ],
    };
    setChartData(data);

    // eslint-disable-next-line
  }, [filter, activeCollection, intervalInYearly]);

  let lastGrowth =
    chartData &&
    chartData.datasets &&
    Array.isArray(chartData.datasets) &&
    chartData.datasets.length > 0
      ? chartData.datasets[0].data[chartData.datasets[0].data.length - 1]
      : 0;

  return (
    <div
      className={classNames(
        "font-sans max-w-full flex flex-col justify-between items-center p-4 bg-white rounded-lg border-2px mt-0",
        {
          "shadow-md": !props.quickView,
          border: !props.quickView,
          "mb-6": !props.quickView,
        }
      )}
    >
      <div
        className={"w-full flex flex-row items-center justify-between mb-10"}
      >
        <div className={"flex flex-col space-y-2"}>
          <h1 className={"text-[23px] font-normal"}>
            [ {activeCollection.collectionName} ] Growth
          </h1>
          <h3 className={"text-[15px] font-normal text-gray-600"}>
            Based on your contribution history
          </h3>
        </div>
        <button
          className={
            "text-[16px] h-[25px] rounded-[100px] bg-[#FD889A] font-medium text-white px-2"
          }
        >
          {currencyFormatter.format(lastGrowth)}
        </button>
      </div>

      <div className="w-full">
        {chartData && (
          <Line
            data={chartData}
            options={{
              scales: {
                x: {
                  title: {
                    display: true,
                    text: "Years",
                  },
                },
                y: {
                  title: {
                    display: true,
                    text: "Investment Value ()",
                  },
                },
              },
            }}
            type="line"
          />
        )}
      </div>

      <div className="w-full py-5">
        <div
          className={
            "flex flex-row align-center font-medium leading-none h-[10px] items-center justify-between"
          }
        >
          <button>Today</button>
          {staticIntervalFilters.map((val, index) => {
            return (
              <button
                key={index}
                onClick={() => setIntervalInYearly(val)}
                className={classNames(
                  "rounded hover:bg-[#FFD6DA] hover:shadow-md p-2",
                  {
                    "bg-[#FFD6DA]": val === intervalInYearly,
                  }
                )}
              >
                {val}y
              </button>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default GrowthChart;
